import { Dispatch } from "react";

import { ActionsUnion, createAction } from "@app/actions/helpers";
import { generateFileReader } from "@app/api";
import { UUID } from "@app/api-types/contracts";
import { IDocument } from "@app/api-types/entities/document";
import { IJob } from "@app/api-types/entities/job";
import { IJobType } from "@app/api-types/entities/jobType";
import { createDocument, getDocuments, ICreateDocumentRequest, ICreateDocumentResult } from "@app/api/v1/documents";
import { createJob, getJob, getJobs, ICreateJobRequest, ICreateJobResult } from "@app/api/v1/jobs";
import { getJobTypes } from "@app/api/v1/jobTypes";
import { IDroppedFile, IJobState } from "@app/types/job";

export const REQUEST_GET_JOBTYPES = "REQUEST_GET_JOBTYPES";
export const RECEIVE_GET_JOBTYPES = "RECEIVE_GET_JOBTYPES";
export const RequestGetJobtypes = () => createAction(REQUEST_GET_JOBTYPES);
export const ReceiveGetJobtypes = (payload: IJobType[]) => createAction(RECEIVE_GET_JOBTYPES, payload);

export const REQUEST_GET_JOBS = "REQUEST_GET_JOBS";
export const RECEIVE_GET_JOBS = "RECEIVE_GET_JOBS";
export const RequestGetJobs = (payload?: boolean) => createAction(REQUEST_GET_JOBS, payload);
export const ReceiveGetJobs = (payload: IJob[]) => createAction(RECEIVE_GET_JOBS, payload);

export const REQUEST_GET_DOCUMENTS = "REQUEST_GET_DOCUMENTS";
export const RECEIVE_GET_DOCUMENTS = "RECEIVE_GET_DOCUMENTS";
export const RequestGetDocuments = (payload?: boolean) => createAction(REQUEST_GET_DOCUMENTS, payload);
export const ReceiveGetDocuments = (payload: IDocument[]) => createAction(RECEIVE_GET_DOCUMENTS, payload);

export const PARSE_DROPPED_FILES = "PARSE_DROPPED_FILES";
export const RECEIVE_DROPPED_FILES = "RECEIVE_DROPPED_FILES";
export const ParseDroppedFiles = (payload: File[]) => createAction(PARSE_DROPPED_FILES, payload);
export const ReceiveDroppedFiles = (payload: IDroppedFile[]) => createAction(RECEIVE_DROPPED_FILES, payload);

export const REQUEST_CREATE_DOCUMENTS = "REQUEST_CREATE_DOCUMENTS";
export const RECEIVE_CREATE_DOCUMENTS = "RECEIVE_CREATE_DOCUMENTS";
export const RequestCreateDocuments = (payload: ICreateDocumentRequest[]) =>
    createAction(REQUEST_CREATE_DOCUMENTS, payload);
export const ReceiveCreateDocuments = (payload: ICreateDocumentResult[]) =>
    createAction(RECEIVE_CREATE_DOCUMENTS, payload);

export const CHANGE_JOBTYPE = "CHANGE_JOBTYPE";
export const ChangeJobtype = (payload: IJobType) => createAction(CHANGE_JOBTYPE, payload);

export const REQUEST_CREATE_JOBS = "REQUEST_CREATE_JOBS";
export const RECEIVE_CREATE_JOBS = "RECEIVE_CREATE_JOBS";
export const RequestCreateJobs = (payload: ICreateJobRequest[]) => createAction(REQUEST_CREATE_JOBS, payload);
export const ReceiveCreateJobs = (payload: ICreateJobResult[]) => createAction(RECEIVE_CREATE_JOBS, payload);

export const JobActions = {
    ChangeJobtype,
    ParseDroppedFiles,
    ReceiveCreateDocuments,
    ReceiveCreateJobs,
    ReceiveDroppedFiles,
    ReceiveGetDocuments,
    ReceiveGetJobs,
    ReceiveGetJobtypes,
    RequestCreateDocuments,
    RequestCreateJobs,
    RequestGetDocuments,
    RequestGetJobs,
    RequestGetJobtypes,
};

export type JobActions = ActionsUnion<typeof JobActions>;

export const useActions = (state: IJobState, dispatch: Dispatch<JobActions>) => {
    const fileReader = generateFileReader(state.fileReaderOptions);

    return {
        DropFiles: async (authKey: string, files: File[], jobType: IJobType) => {
            // parsing the files dropped
            dispatch(ParseDroppedFiles(files));

            const parsedFiles = await Promise.all(files.map(fileReader));
            dispatch(ReceiveDroppedFiles(parsedFiles));

            // producing create-document requests
            const createDocumentRequests: ICreateDocumentRequest[] = parsedFiles
                .filter(v => v.file.data !== null)
                .map<ICreateDocumentRequest>(v => {
                    return { authKey, name: v.file.name, body: v.file.data! };
                });
            if (createDocumentRequests.length === 0) {
                return;
            }

            dispatch(RequestCreateDocuments(createDocumentRequests));

            const receivedDocuments = await Promise.all(createDocumentRequests.map(createDocument));
            dispatch(ReceiveCreateDocuments(receivedDocuments));

            // producing create-job requests
            const createJobRequests: ICreateJobRequest[] = receivedDocuments
                .filter(v => v.errors === null)
                .map<ICreateJobRequest>(v => {
                    return { authKey, auto_start: true, type: jobType.id, input: { document: v.document!.id } };
                });
            if (createJobRequests.length === 0) {
                return;
            }

            dispatch(RequestCreateJobs(createJobRequests));

            const receivedJobs = await Promise.all(createJobRequests.map(createJob));
            dispatch(ReceiveCreateJobs(receivedJobs));
        },
        FetchGetDocuments: async (authKey: string, refetching?: boolean) => {
            dispatch(RequestGetDocuments(refetching));
            dispatch(ReceiveGetDocuments(await getDocuments({ authKey })));
        },
        FetchGetJobs: async (authKey: string, ids: UUID[], refetching?: boolean) => {
            dispatch(RequestGetJobs(refetching));

            if (ids.length === 0) {
                dispatch(ReceiveGetJobs(await getJobs({ authKey })));
            } else {
                const fetchedJobs = await Promise.all(ids.map<Promise<IJob>>(v => getJob({ authKey, id: v })));
                dispatch(ReceiveGetJobs(fetchedJobs));
            }
        },
        FetchGetJobtypes: async () => {
            dispatch(RequestGetJobtypes());
            dispatch(ReceiveGetJobtypes(await getJobTypes()));
        },
        state,
    };
};



