import { IFileReaderOptions } from "@app/api";
import { IErrors } from "@app/api-types/contracts";
import { IDocument } from "@app/api-types/entities/document";
import { IJob, IJobMap } from "@app/api-types/entities/job";
import { IJobType } from "@app/api-types/entities/jobType";
import { ICreateDocumentRequest, ICreateDocumentResult } from "@app/api/v1/documents";
import { ICreateJobRequest, ICreateJobResult } from "@app/api/v1/jobs";
import { FetchLevel, IApiData } from "@app/types/main";

export interface IDroppedFile {
    error: string | null;
    file: {
        name: string;
        data: ArrayBuffer | null;
    };
}

export interface IJobState {
    createJobs: IApiData<IJob[]>;
    jobs: {
        createRequests: ICreateJobRequest[];
        createResults: ICreateJobResult[];
        list: IApiData<IJobMap>;
    };
    jobTypes: IApiData<IJobType[]>;
    fileReaderOptions?: IFileReaderOptions;
    currentJobType: IJobType | null;
    documents: {
        dropped: {
            files: File[];
            errors: IErrors;
        };
        createRequests: ICreateDocumentRequest[];
        createResults: ICreateDocumentResult[];
        list: IApiData<IDocument[]>;
    };
}

export const defaultJobState: IJobState = {
    createJobs: {
        data: [],
        errors: {},
        fetchLevel: FetchLevel.initial,
    },
    currentJobType: null,
    documents: {
        createRequests: [],
        createResults: [],
        dropped: {
            errors: {},
            files: [],
        },
        list: {
            data: [],
            errors: {},
            fetchLevel: FetchLevel.initial,
        },
    },
    fileReaderOptions: {
        mimetypeWhitelist: ["application/pdf"],
        sizeUpperLimit: 10 * 1000 * 1000, // 10MB
    },
    jobTypes: {
        data: [],
        errors: {},
        fetchLevel: FetchLevel.initial,
    },
    jobs: {
        createRequests: [],
        createResults: [],
        list: {
            data: {},
            errors: {},
            fetchLevel: FetchLevel.initial,
        },
    },
};



