import { UUID } from "@app/api-types/contracts";
import { IJobDocument } from "@app/api-types/contracts/job";

export enum JobStatus {
    NOT_STARTED = "not_started",
    WAITING = "waiting",
    RUNNING = "running",
    CANCELLED = "cancelled",
    COMPLETED = "completed",
    FAILED = "failed",
}

export interface IJobProgress {
    current: number;
    total: number;
}

export const getJobMap = (jobs: IJob[]): IJobMap => {
    return jobs.reduce<IJobMap>((out, job) => {
        return { ...out, [job.id]: job };
    }, {});
};

export interface IJobMap {
    [id: string]: IJob;
}

export interface IJob {
    id: UUID;
    status: JobStatus;
    progress: IJobProgress | null;
    documents?: IJobDocument[];
    output?: {
        uri: string;
    };
}



