import React, { createContext, Dispatch, useReducer } from "react";

import { JobActions, useActions } from "@app/actions/job";
import { job } from "@app/reducers";
import { defaultJobState, IJobState } from "@app/types/job";

export interface IJobContext {
    actions: ReturnType<typeof useActions>;
    dispatch: Dispatch<JobActions>;
    state: IJobState;
}

export const JobContext = createContext<IJobContext | null>(null);

interface IOwnProps {
    children: React.ReactChild;
}

type IProps = Readonly<IOwnProps>;

export function JobProvider({ children }: IProps) {
    const [state, dispatch] = useReducer(job, defaultJobState);
    const actions = useActions(state, dispatch);

    return <JobContext.Provider value={{ state, dispatch, actions }}>{children}</JobContext.Provider>;
}



