import { ReceiveGenerateUser, UserActions } from "@app/actions/user";
import { IKindHandlers, Runner } from "@app/reducers/handlers";
import { FetchLevel } from "@app/types/main";
import { IUserState } from "@app/types/user";

const handlers: IKindHandlers<IUserState, UserActions> = {
    user: {
        generate: {
            receive: (state: IUserState, action: ReturnType<typeof ReceiveGenerateUser>): IUserState => {
                if (action.payload.errors !== null) {
                    return {
                        ...state,
                        user: {
                            ...state.user,
                            data: null,
                            errors: action.payload.errors,
                            fetchLevel: FetchLevel.failure,
                        },
                    };
                }

                const {
                    isDemo,
                    api_keys: { live: liveKey, test: testKey },
                } = action.payload.user!;
                const userAuth = btoa(`${isDemo ? testKey : liveKey}:`);

                return {
                    ...state,
                    user: {
                        ...state.user,
                        data: action.payload.user,
                        errors: {},
                        fetchLevel: FetchLevel.success,
                    },
                    userAuth,
                };
            },
            request: (state: IUserState): IUserState => {
                return {
                    ...state,
                    user: { ...state.user, errors: {}, fetchLevel: FetchLevel.fetching },
                };
            },
        },
    },
};

export const run: Runner<IUserState, UserActions> = (state: IUserState, action: UserActions): IUserState => {
    const [kind, verb, task] = action.type
        .split("_")
        .reverse()
        .map(v => v.toLowerCase());
    if (!(kind in handlers)) {
        return state;
    }
    const kindHandlers = handlers[kind];

    if (!(verb in kindHandlers)) {
        return state;
    }
    const verbHandlers = kindHandlers[verb];

    if (!(task in verbHandlers)) {
        return state;
    }
    const taskHandler = verbHandlers[task];

    return taskHandler(state, action);
};



