import React, { useContext, useEffect } from "react";

import useInterval from "use-interval";

import { UUID } from "@app/api-types/contracts";
import { IDocument } from "@app/api-types/entities/document";
import { IJob, IJobMap, JobStatus } from "@app/api-types/entities/job";
import { IUser } from "@app/api-types/entities/user";
import { NonJobs } from "@app/App/Jobs/NonJobs";
import { JobContext } from "@app/context-containers/Job";
import { UserContext } from "@app/context-containers/User";
import { FetchLevel } from "@app/types/main";

interface IStatusLabelMap {
    [status: string]: string;
}

const statusLabelMap: IStatusLabelMap = {
    [JobStatus.WAITING]: "Pending",
    [JobStatus.RUNNING]: "Running",
};

const renderStatusLabel = (status: JobStatus) => {
    if (!(status in statusLabelMap)) {
        return status;
    }

    return statusLabelMap[status];
};

interface IStatusBadgeMap {
    [status: string]: string;
}

const statusBadgeMap: IStatusBadgeMap = {
    [JobStatus.WAITING]: "badge-warning",
    [JobStatus.RUNNING]: "badge-warning",
    [JobStatus.COMPLETED]: "badge-success",
    [JobStatus.FAILED]: "badge-danger",
};

const renderStatusBadge = (status: JobStatus) => {
    if (!(status in statusBadgeMap)) {
        return "";
    }

    return statusBadgeMap[status];
};

const renderStatus = (status: JobStatus) => {
    return <span className={`badge ${renderStatusBadge(status)}`}>{renderStatusLabel(status)}</span>;
};

const renderDocument = (document: IDocument | null) => {
    if (document === null) {
        return <em>no documents</em>;
    }

    return document.name;
};

const renderProgress = (job: IJob, user: IUser | null) => {
    if (job.status === JobStatus.COMPLETED) {
        if (typeof job.output === "undefined") {
            return (
                <div className="progress">
                    <div
                        className="progress-bar"
                        role="progressbar"
                        aria-valuenow={100}
                        aria-valuemin={0}
                        aria-valuemax={100}
                        style={{ width: "100%" }}
                    >
                        100%
                    </div>
                </div>
            );
        }

        if (user === null) {
            return (
                <a href={job.output.uri} target="_blank">
                    View
                </a>
            );
        }

        return (
            <a href={`${job.output.uri}?token=${user.api_keys.test}`} target="_blank">
                View
            </a>
        );
    }

    if (job.progress === null) {
        return (
            <div className="progress">
                <div
                    className="progress-bar"
                    role="progressbar"
                    aria-valuenow={0}
                    aria-valuemin={0}
                    aria-valuemax={100}
                    style={{ width: "0%" }}
                >
                    0%
                </div>
            </div>
        );
    }

    const percentageProgress = Number(((job.progress.current / job.progress.total) * 100).toFixed(2));

    return (
        <div className="progress">
            <div
                className="progress-bar"
                role="progressbar"
                aria-valuenow={percentageProgress}
                aria-valuemin={0}
                aria-valuemax={100}
                style={{ width: `${percentageProgress}%` }}
            >
                {`${percentageProgress}%`}
            </div>
        </div>
    );
};

const renderJob = (job: IJob, document: IDocument | null, user: IUser | null, i: number) => (
    <tr key={i}>
        <td className="first-column">{i + 1}.</td>
        <td className="second-column">{renderDocument(document)}</td>
        <td className="third-column">{renderStatus(job.status)}</td>
        <td className="fourth-column">{renderProgress(job, user)}</td>
        <td className="sixth-column">
            <a href="#">×</a>
        </td>
    </tr>
);

const renderJobs = (jobs: IJobMap, documents: IDocument[], user: IUser | null, hasJobsData: boolean) => {
    if (!hasJobsData) {
        return (
            <tr>
                <td colSpan={6}>
                    <em>No jobs queued.</em>
                </td>
            </tr>
        );
    }

    return Object.keys(jobs).map((jobId, i) => {
        const job = jobs[jobId];

        const foundDocument = documents.reduce<IDocument | null>((out, document) => {
            if (out !== null) {
                return out;
            }

            if (typeof job.documents === "undefined") {
                return null;
            }

            if (document.id === job.documents[0].id) {
                return document;
            }

            return null;
        }, null);

        return renderJob(job, foundDocument, user, i);
    });
};

export function Jobs() {
    // hooks
    const {
        state: {
            jobs: { list: jobs, createRequests: createJobsRequests },
            documents: {
                list: documents,
                dropped,
                createRequests: createDocumentRequests,
                createResults: createDocumentResults,
            },
        },
        actions: { FetchGetJobs },
    } = useContext(JobContext)!;
    const {
        state: { userAuth, user },
    } = useContext(UserContext)!;

    useInterval(async () => {
        if (userAuth === null) {
            return;
        }

        const jobIdsToCheck = Object.keys(jobs.data)
            .filter(v => [JobStatus.WAITING, JobStatus.RUNNING, !JobStatus].indexOf(jobs.data[v].status) > -1)
            .map<UUID>(v => jobs.data[v].id);
        if (jobIdsToCheck.length === 0) {
            return;
        }

        await FetchGetJobs(userAuth, jobIdsToCheck, true);
    }, 2.5 * 1000);

    useEffect(() => {
        (async () => {
            if (userAuth === null) {
                return;
            }

            if (jobs.fetchLevel === FetchLevel.initial) {
                await FetchGetJobs(userAuth, []);

                return;
            }
        })();
    }, [jobs.fetchLevel, userAuth]);

    // content rendering
    switch (jobs.fetchLevel) {
        case FetchLevel.success:
        case FetchLevel.refetching:
            break;
        default:
        case FetchLevel.fetching:
        case FetchLevel.initial:
            return (
                <p>
                    <em>Loading jobs...</em>
                </p>
            );
    }

    const hasJobsData =
        Object.keys(jobs.data).length > 0 ||
        createJobsRequests.length > 0 ||
        dropped.files.length > 0 ||
        createDocumentRequests.length > 0 ||
        createDocumentResults.length > 0 ||
        Object.keys(dropped.errors).length > 0;

    return (
        <div className="row">
            <div className="col pt-3">
                <div className="table-responsive file-list">
                    <table className="table table-striped table-hover table-sm">
                        <thead>
                            <tr>
                                <th className="first-column">#</th>
                                <th className="second-column">File</th>
                                <th className="third-column">Status</th>
                                <th className="fourth-column">Progress</th>
                                <th className="sixth-column">×</th>
                            </tr>
                        </thead>
                        <tbody>
                            {renderJobs(jobs.data, documents.data, user.data, hasJobsData)}
                            <NonJobs />
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}



