import * as HTTPStatus from "http-status";

import { IApiErrorResponse, IApiListParams, IAuthenticatedRequest, IErrors, UUID } from "@app/api-types/contracts";
import { ICreateJobResponse, IGetJobs } from "@app/api-types/contracts/job";
import { IJob } from "@app/api-types/entities/job";
import { apiEndpointV1, request, requestWithQuery } from "@app/api/v1/index";

export interface IJobInputByDocument {
    document: string; // document id
}

export interface IJobInputByContents {
    contents: string; // string-encoded buffer
}

export type JobInput = IJobInputByDocument | IJobInputByContents;

export interface ICreateJobRequest extends IAuthenticatedRequest {
    type: string; // job-type id
    auto_start: boolean;
    input: JobInput;
}

export interface ICreateJobResult {
    input: JobInput;
    response: ICreateJobResponse | null;
    errors: IErrors | null;
}

export const createJob = async (req: ICreateJobRequest): Promise<ICreateJobResult> => {
    const res = await request<ICreateJobRequest, ICreateJobResponse | IApiErrorResponse>({
        body: req,
        headers: new Headers({
            authorization: `Basic ${req.authKey}`,
            "content-type": "application/json",
        }),
        method: "POST",
        url: `${apiEndpointV1}/jobs`,
    });

    switch (res.status) {
        case HTTPStatus.UNAUTHORIZED:
        case HTTPStatus.BAD_REQUEST:
            return {
                errors: { error: (res.body!.response as IApiErrorResponse)!.message },
                input: req.input,
                response: null,
            };
        case HTTPStatus.CREATED:
            return {
                errors: null,
                input: req.input,
                response: res.body!.response as ICreateJobResponse,
            };
        default:
            return {
                errors: { error: "Unknown response code" },
                input: req.input,
                response: null,
            };
    }
};

export interface IGetJobsRequest {
    query?: IApiListParams;
    authKey: string;
}

export const getJobs = async (req: IGetJobsRequest): Promise<IJob[]> => {
    const res = await requestWithQuery<IApiListParams, IGetJobs>({
        headers: new Headers({
            authorization: `Basic ${req.authKey}`,
            "content-type": "application/json",
        }),
        method: "GET",
        query: req.query,
        url: `${apiEndpointV1}/jobs`,
    });

    return res.body!.response.list;
};

export interface IGetJobRequest {
    id: UUID;
    authKey: string;
}

export const getJob = async (req: IGetJobRequest): Promise<IJob> => {
    const res = await request<null, IJob>({
        headers: new Headers({
            authorization: `Basic ${req.authKey}`,
            "content-type": "application/json",
        }),
        method: "GET",
        url: `${apiEndpointV1}/jobs/${req.id}`,
    });

    return res.body!.response;
};



