import React, { useContext } from "react";

import { IErrors } from "@app/api-types/contracts";
import { IDocument } from "@app/api-types/entities/document";
import { ICreateDocumentRequest, ICreateDocumentResult } from "@app/api/v1/documents";
import { ICreateJobRequest, JobInput } from "@app/api/v1/jobs";
import { JobContext } from "@app/context-containers";

// dropped-files
const renderDroppedFile = (file: File, i: number, offset: number) => {
    const key = i + offset;

    return (
        <tr key={key}>
            <td className="first-column">{key + 1}.</td>
            <td className="second-column">
                {file.name} ({(file.size / 1000 / 1000).toFixed(2)}MB)
            </td>
            <td className="third-column">
                <span className="badge badge-warning">PARSING DOCUMENT</span>
            </td>
            <td className="fourth-column">
                <div className="progress">
                    <div
                        className="progress-bar"
                        role="progressbar"
                        aria-valuenow={0}
                        aria-valuemin={0}
                        aria-valuemax={100}
                        style={{ width: "0%" }}
                    >
                        0%
                    </div>
                </div>
            </td>
            <td className="sixth-column">
                <a href="#">×</a>
            </td>
        </tr>
    );
};

const renderDroppedFiles = (files: File[], offset: number) => {
    if (files.length === 0) {
        return null;
    }

    return files.map((v, i) => renderDroppedFile(v, i, offset));
};

// drop-errors
const renderDropError = (filename: string, value: string, i: number, offset: number) => {
    const key = i + offset;

    return (
        <tr key={key}>
            <td className="first-column">{key + 1}.</td>
            <td className="second-column">
                <strong>{filename}</strong>: {value}
            </td>
            <td className="third-column">
                <span className="badge badge-danger">FAILED PARSING DOCUMENT</span>
            </td>
            <td className="fourth-column">
                <div className="progress">
                    <div
                        className="progress-bar"
                        role="progressbar"
                        aria-valuenow={0}
                        aria-valuemin={0}
                        aria-valuemax={100}
                        style={{ width: "0%" }}
                    >
                        0%
                    </div>
                </div>
            </td>
            <td className="sixth-column">
                <a href="#">×</a>
            </td>
        </tr>
    );
};

const renderDropErrors = (errors: IErrors, offset: number) => {
    if (Object.keys(errors).length === 0) {
        return null;
    }

    return Object.keys(errors).map((k, i) => renderDropError(k, errors[k], i, offset));
};

// create-document requests
const renderCreateDocumentRequest = (request: ICreateDocumentRequest, i: number, offset: number) => {
    const key = i + offset;

    return (
        <tr key={key}>
            <td className="first-column">{key + 1}.</td>
            <td className="second-column">{request.name}</td>
            <td className="third-column">
                <span className="badge badge-warning">UPLOADING DOCUMENT</span>
            </td>
            <td className="fourth-column">
                <div className="progress">
                    <div
                        className="progress-bar"
                        role="progressbar"
                        aria-valuenow={0}
                        aria-valuemin={0}
                        aria-valuemax={100}
                        style={{ width: "0%" }}
                    >
                        0%
                    </div>
                </div>
            </td>
            <td className="sixth-column">
                <a href="#">×</a>
            </td>
        </tr>
    );
};

const renderCreateDocumentRequests = (requests: ICreateDocumentRequest[], offset: number) => {
    if (requests.length === 0) {
        return null;
    }

    return requests.map((v, i) => renderCreateDocumentRequest(v, i, offset));
};

// create-document results
const renderCreateDocumentResultError = (key: string, value: string, i: number) => <li key={i}>{value}</li>;

const renderCreateDocumentResultErrors = (errors: IErrors) => {
    return <ul>{Object.keys(errors).map((k, i) => renderCreateDocumentResultError(k, errors[k], i))}</ul>;
};

const renderCreateDocumentResultDocument = (document: IDocument, i: number, offset: number) => {
    const key = i + offset;

    return (
        <tr key={key}>
            <td className="first-column">{key + 1}.</td>
            <td className="second-column">{document.name}</td>
            <td className="third-column">
                <span className="badge badge-warning">UPLOADED DOCUMENT</span>
            </td>
            <td className="fourth-column">
                <div className="progress">
                    <div
                        className="progress-bar"
                        role="progressbar"
                        aria-valuenow={0}
                        aria-valuemin={0}
                        aria-valuemax={100}
                        style={{ width: "0%" }}
                    >
                        0%
                    </div>
                </div>
            </td>
            <td className="sixth-column">
                <a href="#">×</a>
            </td>
        </tr>
    );
};

const renderCreateDocumentResult = (result: ICreateDocumentResult, i: number, offset: number) => {
    const key = i + offset;

    if (result.errors !== null) {
        return (
            <li key={key}>
                There were errors creating document <strong>{result.name}</strong>:
                {renderCreateDocumentResultErrors(result.errors)}
            </li>
        );
    }

    if (result.document !== null) {
        return renderCreateDocumentResultDocument(result.document, i, offset);
    }

    return null;
};

const renderCreateDocumentResults = (results: ICreateDocumentResult[], offset: number) => {
    if (results.length === 0) {
        return null;
    }

    return results.map((v, i) => renderCreateDocumentResult(v, i, offset));
};

// create-job requests
const renderCreateJobRequestInput = (input: JobInput, document: IDocument | null) => {
    if ("document" in input) {
        if (document === null) {
            return (
                <>
                    File <strong>{input.document}</strong>
                </>
            );
        }

        return document.name;
    }

    if ("contents" in input) {
        return (
            <>
                Raw file <strong>{(input.contents.length / 1000 / 1000).toFixed(2)}MB</strong>
            </>
        );
    }

    return null;
};

const renderCreateJobRequest = (request: ICreateJobRequest, i: number, offset: number, documents: IDocument[]) => {
    const key = i + offset;

    const document: IDocument | null = documents.reduce<IDocument | null>((out, v) => {
        if (out !== null) {
            return out;
        }

        if (!("document" in request.input)) {
            return null;
        }

        if (v.id === request.input.document) {
            return v;
        }

        return null;
    }, null);

    return (
        <tr key={key}>
            <td className="first-column">{key + 1}.</td>
            <td className="second-column">{renderCreateJobRequestInput(request.input, document)}</td>
            <td className="third-column">
                <span className="badge badge-warning">CREATING JOB</span>
            </td>
            <td className="fourth-column">
                <div className="progress">
                    <div
                        className="progress-bar"
                        role="progressbar"
                        aria-valuenow={0}
                        aria-valuemin={0}
                        aria-valuemax={100}
                        style={{ width: "0%" }}
                    >
                        0%
                    </div>
                </div>
            </td>
            <td className="sixth-column">
                <a href="#">×</a>
            </td>
        </tr>
    );
};

const renderCreateJobRequests = (requests: ICreateJobRequest[], offset: number, documents: IDocument[]) => {
    if (requests.length === 0) {
        return null;
    }

    return requests.map((v, i) => renderCreateJobRequest(v, i, offset, documents));
};

export function NonJobs() {
    // job-context
    const {
        state: {
            jobs: {
                createRequests: createJobRequests,
                list: { data: jobs },
            },
            documents: {
                dropped,
                createRequests: createDocumentRequests,
                createResults: createDocumentResults,
                list: { data: documents },
            },
        },
    } = useContext(JobContext)!;

    const countOffset = Object.keys(jobs).length;

    return (
        <>
            {renderDroppedFiles(dropped.files, countOffset)}
            {renderDropErrors(dropped.errors, countOffset)}
            {renderCreateDocumentRequests(createDocumentRequests, countOffset)}
            {renderCreateDocumentResults(createDocumentResults, countOffset)}
            {renderCreateJobRequests(createJobRequests, countOffset, documents)}
        </>
    );
}



