import { CHANGE_JOBTYPE, JobActions } from "@app/actions/job";
import { IJobState } from "@app/types/job";
import { runners } from "./handlers";

type State = Readonly<IJobState>;

export const job = (state: State, action: JobActions): State => {
    switch (action.type) {
        case CHANGE_JOBTYPE:
            return { ...state, currentJobType: action.payload };
        default:
            break;
    }

    return runners.jobRunner(state, action);
};



