import { IGetJobTypes } from "@app/api-types/contracts/jobTypes";
import { IJobType } from "@app/api-types/entities/jobType";
import { apiEndpointV1, request } from "@app/api/v1/index";

export const getJobTypes = async (): Promise<IJobType[]> => {
    const res = await request<null, IGetJobTypes>({ url: `${apiEndpointV1}/job-types` });

    return res.body!.response.list;
};



