import { Dispatch } from "react";

import { ActionsUnion, createAction } from "@app/actions/helpers";
import { generateUser, IGenerateUserResult } from "@app/api/v1/users";
import { IUserState } from "@app/types/user";

export const REQUEST_GENERATE_USER = "REQUEST_GENERATE_USER";
export const RECEIVE_GENERATE_USER = "RECEIVE_GENERATE_USER";
export const RequestGenerateUser = () => createAction(REQUEST_GENERATE_USER);
export const ReceiveGenerateUser = (payload: IGenerateUserResult) => createAction(RECEIVE_GENERATE_USER, payload);

export const JobActions = {
    ReceiveGenerateUser,
    RequestGenerateUser,
};

export type UserActions = ActionsUnion<typeof JobActions>;

export const useActions = (state: IUserState, dispatch: Dispatch<UserActions>) => {
    return {
        FetchGenerateUser: async () => {
            dispatch(RequestGenerateUser());
            dispatch(ReceiveGenerateUser(await generateUser()));
        },
    };
};



