import * as HTTPStatus from "http-status";

import { IApiErrorResponse, IApiListParams, IAuthenticatedRequest, IErrors } from "@app/api-types/contracts";
import { ICreateDocumentResponse, IGetDocuments } from "@app/api-types/contracts/document";
import { IDocument } from "@app/api-types/entities/document";
import { apiEndpointV1, request, requestWithQuery } from "@app/api/v1/index";

export interface ICreateDocumentRequest extends IAuthenticatedRequest {
    name: string;
    body: ArrayBuffer;
}

export interface ICreateDocumentResult {
    name: string;
    document: IDocument | null;
    errors: IErrors | null;
}

export const createDocument = async (req: ICreateDocumentRequest): Promise<ICreateDocumentResult> => {
    const res = await request<ArrayBuffer, ICreateDocumentResponse | IApiErrorResponse>({
        body: req.body,
        headers: new Headers({
            authorization: `Basic ${req.authKey}`,
            "content-type": "application/octet-stream",
            "x-name": req.name,
        }),
        method: "POST",
        url: `${apiEndpointV1}/documents`,
    });

    switch (res.status) {
        case HTTPStatus.UNAUTHORIZED:
        case HTTPStatus.BAD_REQUEST:
            return {
                document: null,
                errors: { error: (res.body!.response as IApiErrorResponse)!.message },
                name: req.name,
            };
        case HTTPStatus.CREATED:
            const data = res.body!.response as ICreateDocumentResponse;

            return {
                document: { id: data.id, name: data.name, type: data.type, size: data.size },
                errors: null,
                name: req.name,
            };
        default:
            return {
                document: null,
                errors: { error: "Unknown response code" },
                name: req.name,
            };
    }
};

export interface IGetDocumentsRequest {
    query?: IApiListParams;
    authKey: string;
}

export const getDocuments = async (req: IGetDocumentsRequest): Promise<IDocument[]> => {
    const res = await requestWithQuery<IApiListParams, IGetDocuments>({
        headers: new Headers({
            authorization: `Basic ${req.authKey}`,
            "content-type": "application/json",
        }),
        method: "GET",
        query: req.query,
        url: `${apiEndpointV1}/documents`,
    });

    return res.body!.response.list;
};



