import * as HTTPStatus from "http-status";

import { IApiErrorResponse, IErrors } from "@app/api-types/contracts";
import { IGenerateUserRequest, IGenerateUserResponse } from "@app/api-types/contracts/user";
import { IUser } from "@app/api-types/entities/user";
import { apiEndpointV1, request } from "@app/api/v1/index";

export interface IGenerateUserResult {
    user: IUser | null;
    errors: IErrors | null;
}

export const generateUser = async (): Promise<IGenerateUserResult> => {
    const res = await request<IGenerateUserRequest, IGenerateUserResponse | IApiErrorResponse>({
        body: { demo: true },
        method: "POST",
        url: `${apiEndpointV1}/users`,
    });

    switch (res.status) {
        case HTTPStatus.BAD_REQUEST:
            return {
                errors: { error: (res.body!.response as IApiErrorResponse)!.message },
                user: null,
            };
        case HTTPStatus.CREATED:
            return {
                errors: null,
                user: res.body!.response as IGenerateUserResponse,
            };
        default:
            return {
                errors: { error: "Unknown response code" },
                user: null,
            };
    }
};



